    meta: '#0866FF',
    'meta ads': '#0866FF',
    facebook: '#0866FF',
    google: '#4285F4',
    'google ads': '#4285F4',
    tiktok: '#00F2EA',
    'tiktok ads': '#00F2EA',
    snapchat: '#FFFC00',
    'snapchat ads': '#FFFC00',
    whatsapp: '#25D366',
    // Other platforms
    instagram: '#E4405F',
    linkedin: '#0A66C2',
    twitter: '#1DA1F2',
    x: '#000000',
    youtube: '#FF0000',
    pinterest: '#E60023',
    // Brands
    mercedes: '#A0A0A0',
    'mercedes-benz': '#A0A0A0',
    bmw: '#0066B1',
    nike: '#111111',
    adidas: '#000000',
    apple: '#A2AAAD',
    samsung: '#1428A0',
    amazon: '#FF9900',
    netflix: '#E50914',
    spotify: '#1DB954',
